import axios from 'axios'
import account from './account'

let router;

axios.defaults.timeout = 5000
axios.defaults.withCredentials = true
axios.defaults.xsrfHeaderName = account.AccountKey

//请求拦截
axios.interceptors.request.use(
  config => {
    if (account.getToken()) {
      config.headers[account.AccountKey] = account.getToken() // 让每个请求携带自定义token 请根据实际情况自行修改
    }
    return config;
  },
  error => Promise.reject(error)
)

//请求结果拦截
axios.interceptors.response.use(
  res => Promise.resolve(res),
  error => {
    console.log(error.response);
    if (typeof (error.response) == "undefined") {
      return Promise.reject(error);
    }
    switch (error.response.status) {
      case 401:
        account.removeToken();
        router.replace({
          path: '/login',
          query: {
            redirectUri: router.currentRoute.fullPath
          }
        });
        break;
      case 500:
        router.replace({
          path: '/500',
        });
        break;
      case 404:
        router.replace({
          path: '/404',
        });
        break;
      case 403:
        router.replace({
          path: '/403',
        });
        break;
      default:
        break;
    }
    return Promise.reject(err);
  }
)

// 认证类型
const AUTH_TYPE = {
  BEARER: 'Bearer',
  BASIC: 'basic',
  AUTH1: 'auth1',
  AUTH2: 'auth2',
}

// http method
const METHOD = {
  GET: 'get',
  POST: 'post',
  PUT: 'put',
  DELETE: "delete"
}

/**
 * axios请求
 * @param url 请求地址
 * @param method {METHOD} http method
 * @param params 请求参数
 * @returns {Promise<AxiosResponse<T>>}
 */
async function request(url, method, params) {
  switch (method) {
    case METHOD.GET:
      return axios.get(url, { params })
    case METHOD.POST:
      return axios.post(url, params)
    case METHOD.PUT:
      return axios.put(url, params)
    case METHOD.DELETE:
      return axios.delete(url, { params })
    default:
      return axios.get(url, { params })
  }
}

/**
 * 设置认证信息
 * @param token {string}
 * @param authType {AUTH_TYPE} 认证类型，默认：{AUTH_TYPE.BEARER}
 */
function setAuthorization(token, authType = AUTH_TYPE.BEARER) {
  switch (authType) {
    case AUTH_TYPE.BEARER:
      account.setToken('Bearer ' + token)
      break
    case AUTH_TYPE.BASIC:
    case AUTH_TYPE.AUTH1:
    case AUTH_TYPE.AUTH2:
    default:
      break
  }
}

/**
 * 移出认证信息
 * @param authType {AUTH_TYPE} 认证类型
 */
function removeAuthorization(authType = AUTH_TYPE.BEARER) {
  switch (authType) {
    case AUTH_TYPE.BEARER:
      account.removeToken();
      break
    case AUTH_TYPE.BASIC:
    case AUTH_TYPE.AUTH1:
    case AUTH_TYPE.AUTH2:
    default:
      break
  }
}

/**
 * 检查认证信息
 * @param authType
 * @returns {boolean}
 */
function checkAuthorization(authType = AUTH_TYPE.BEARER) {
  switch (authType) {
    case AUTH_TYPE.BEARER:
      if (account.getToken()) {
        return true
      }
      break
    case AUTH_TYPE.BASIC:
    case AUTH_TYPE.AUTH1:
    case AUTH_TYPE.AUTH2:
    default:
      break
  }
  return false
}

function install(Vue) {
  Vue.$request = this;
  Vue.prototype.$request = this;
}

export default function (inputRouter) {
  router = inputRouter;
  if (router == null) {
    console.error("request need used after router.");
  }
  return {
    install,
  }
}



