import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/login'
import Blank from '@/pages/blank'
import Home from '@/pages/home'
import Project from '@/pages/project'
import Tools from '@/pages/tools'
import Settings from '@/pages/settings'
import routerUtil from '@/utils/router'
Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Cag Studio',
      meta: {
        requireAuth: true,
        redirect: "/application/home"
      },
      component: Blank
    },
    {
      path: '/application',
      name: 'Application',
      meta: {
        requireAuth: true,
      },
      component: Blank,
      children: [
        {
          path: 'home',
          name: '首页',
          meta: {
            requireAuth: true,
          },
          component: Home
        },
        {
          path: 'project',
          name: '项目管理',
          meta: {
            requireAuth: true,
          },
          component: Project
        },
        {
          path: 'tools',
          name: '工具箱',
          meta: {
            requireAuth: true,
          },
          component: Tools
        },
        {
          path: 'system',
          name: '系统设置',
          meta: {
            requireAuth: true,
          },
          component: Settings
        },
      ]
    },
    {
      path: '/login',
      name: 'Login',
      meta: {
        requireAuth: false,
      },
      component: Login
    },
    {
      path: '/403',
      name: '403',
      meta: {
        requireAuth: false,
      },
      component: () => import('@/pages/exception/403'),
    },
    {
      path: '/500',
      name: '500',
      meta: {
        requireAuth: false,
      },
      component: () => import('@/pages/exception/500'),
    },
    {
      path: '*',
      name: '404',
      meta: {
        requireAuth: false,
      },
      component: () => import('@/pages/exception/404'),
    }
  ]
})

routerUtil.addRouterWatcher(router)

export default router



