import account from "./account";

Array.prototype.indexOf = function (val) {
  for (var i = 0; i < this.length; i++) {
    if (this[i] == val) return i;
  }
  return -1;
};
Array.prototype.remove = function (val) {
  var index = this.indexOf(val);
  if (index > -1) {
    this.splice(index, 1);
  }
};

Array.prototype.firstOrDefault = function (action) {
  for (var i = 0; i < this.length; i++) {
    if (action(this[i]))
      return this[i];
  }
  return undefined;
}

Array.prototype.where = function (action) {
  var back = [];
  for (var i = 0; i < this.length; i++) {
    if (action(this[i]))
      back.push(this[i]);
  }
  return back;
}


Array.prototype.first = function (action) {
  for (var i = 0; i < this.length; i++) {
    if (action(this[i]))
      return this[i];
  }
  throw "Not Found";
}



//添加路由监听
function addRouterWatcher(router) {
  if (!(router && router.beforeEach)) {
    console.error("Add Router Watcher Error.");
    return;
  }
  router.beforeEach((to, from, next) => {
    //不需要鉴权
    if (to.meta && to.meta.redirect) {
      next({ path: to.meta.redirect })
    }
    //不需要鉴权
    if (!(to.meta && to.meta.requireAuth)) {
      next()
    }
    //需要鉴权
    if (to.meta && to.meta.requireAuth) {
      //判断是否登录
      if (account.getToken()) {
        next()
      }
      else {
        //未登录则进行登录跳转
        next({ path: '/login' })
      }
    }
  })
}

export default {
  addRouterWatcher,
}



