<template>
  <t-layout class="layout-page-layout">
    <t-header>
      <t-head-menu
        :theme="theme"
        :defaultValue="nowRoute"
        @change="routeChange"
      >
        <slot slot="logo" name="logo" />
        <t-menu-item v-for="(item, i) in router" :key="i" :value="item.path">{{
          item.name
        }}</t-menu-item>
        <template #operations v-if="isForm">
          <slot name="operations" />
          <t-tooltip content="最小化" placement="bottom" show-arrow>
            <a href="javascript:;"
              ><icon
                class="t-menu__operations-icon"
                name="remove"
                @click="minimize"
            /></a>
          </t-tooltip>
          <t-tooltip content="最大化或恢复正常" placement="bottom" show-arrow>
            <a href="javascript:;"
              ><icon
                class="t-menu__operations-icon"
                :name="maxOrNomal"
                @click="maximizeOrNomal"
            /></a>
          </t-tooltip>
          <t-tooltip content="关闭窗口" placement="bottom" show-arrow>
            <a href="javascript:;"
              ><icon
                class="t-menu__operations-icon"
                name="close"
                @click="close"
            /></a>
          </t-tooltip>
        </template>
      </t-head-menu>
    </t-header>
    <t-content>
      <slot name="content" />
    </t-content>
    <t-dialog
      header="请确认"
      body="是否关闭应用？"
      :visible.sync="visible"
      :onConfirm="onConfirmAnother"
      :onCancel="onCancel"
      :onEscKeydown="onCancel"
      :onCloseBtnClick="onCancel"
      :onOverlayClick="onCancel"
      :onClose="onCancel"
    ></t-dialog>
  </t-layout>
</template>

<script>
import { Icon } from "tdesign-icons-vue";
import form from "@/utils/form";
export default {
  data() {
    return {
      theme: "light",
      isForm: form.IsForm(),
      maxOrNomal: "fullscreen",
      visible: false,
      router: this.$router.options.routes.first((c) => c.path == "/application")
        .children,
      nowRoute: this.$route.path.split("/")[2],
    };
  },
  components: {
    Icon,
  },
  mounted() {
    form.AddStatusChangeEventListener(this.statusChange);
  },
  methods: {
    statusChange() {
      switch (form.GetFormStatus()) {
        case "Normal":
          this.maxOrNomal = "fullscreen";
          break;
        case "Maximized":
          this.maxOrNomal = "fullscreen-exit";
          break;
      }
    },
    minimize() {
      form.MinimizeForm();
    },
    maximizeOrNomal() {
      form.MaximizeOrNomalForm();
    },
    close() {
      this.visible = true;
    },
    onConfirmAnother() {
      form.CloseForm();
    },
    onCancel(context) {},
    routeChange(menuValue) {
      this.$router.push({ path: menuValue });
    },
  },
};
</script>

<style lang="less">
@import "index";
</style>



