<template>
  <div id="app" :style="style">
    <router-view />
  </div>
</template>

<script>
import form from "@/utils/form";
export default {
  name: "App",
  data() {
    return {
      style: {
        width: form.GetFormWidth() + "px",
        height: form.GetFormHeight() + "px",
      },
    };
  },
  mounted() {
    let that = this;
    window.onresize = function () {
      that.style.width = form.GetFormWidth() + "px";
      that.style.height = form.GetFormHeight() + "px";
    };
  },
  methods: {},
};
</script>

<style lang="less">
@import url("App");
</style>



