<template>
  <div class="login-card">
    <t-row>
      <t-col :span="12" class="login-card-header">
        <h1>CAG Studio</h1>
      </t-col>
    </t-row>
    <t-divider></t-divider>
    <t-form
      :data="info"
      ref="form"
      @submit="login"
      :colon="true"
      :labelWidth="0"
    >
      <div class="login-card-centent">
        <t-row class="login-card-centent-item">
          <t-col :span="12">
            <t-form-item name="account">
              <t-input
                clearable
                v-model="info.account"
                placeholder="请输入账号"
                @clear="onClear"
              >
                <user-icon slot="prefix-icon"></user-icon
              ></t-input> </t-form-item
          ></t-col>
        </t-row>
        <t-row class="login-card-centent-item">
          <t-col :span="12">
            <t-form-item name="password">
              <t-input
                type="password"
                clearable
                v-model="info.password"
                placeholder="请输入密码"
              >
                <lock-on-icon
                  slot="prefix-icon"
                ></lock-on-icon> </t-input></t-form-item
          ></t-col>
        </t-row>
        <span class="login-card-remember-password"
          >记住密码:&nbsp;<t-switch size="small" v-model="needRemember"
        /></span>
        <a class="login-card-sign-up" href="javascript:;" @click="signUp"
          >注册账号?</a
        >
      </div>
      <t-divider></t-divider>
      <div class="login-card-footer">
        <t-row>
          <t-col :span="12">
            <t-button
              theme="default"
              shape="round"
              variant="outline"
              style="margin: 9px"
              :loading="loading"
              type="submit"
            >
              <icon slot="icon" name="login" />
              开始创造!</t-button
            >
          </t-col>
        </t-row>
      </div>
    </t-form>
  </div>
</template>

<script>
import { Icon, LockOnIcon, UserIcon } from "tdesign-icons-vue";
import account from "@/utils/account";
export default {
  name: "LoginCard",
  data() {
    return {
      loading: false,
      info: {
        account: "",
        password: "",
      },
      needRemember: false,
    };
  },
  components: {
    Icon,
    LockOnIcon,
    UserIcon,
  },
  methods: {
    login({ validateResult, firstError }) {
      this.loading = true;
      if (validateResult === true) {
        account.setToken("123456789");
        account.setUserInfo({ name: "123" });
        this.loading = false;
        this.$message.success({
          content: "提交成功",
          placement: "bottom-right",
        });
        this.$router.push({ path: "/application/home" });
      } else {
        this.loading = false;
        console.log("Errors: ", validateResult);
        this.$message.warning({
          content: firstError,
          placement: "bottom-right",
        });
      }
    },
    onClear() {},
    signUp() {
      this.$emit("signup", {});
    },
  },
};
</script>


<style lang="less">
@import "index";
</style>



