<template>
  <bgLayout>
    <iframe
      src="http://cag.studio.com/infinitown/index.html"
      width="1920"
      height="1080"
      frameborder="no"
      border="0"
      marginwidth="0"
      marginheight="0"
      scrolling="no"
      allowtransparency="yes"
      slot="bg"
      v-if="isForm"
    />
    <div slot="bg" class="login-bg" v-if="!isForm" />
    <div class="login-content" slot="content">
      <LoginCard v-if="!needSignup" @signup="showSignup" />
      <SignupCard v-if="needSignup" @login="showLogin" />

      <div class="login-topbar" v-if="isForm">
        <a href="javascript:;" class="login-close"
          ><icon
            class="t-menu__operations-icon"
            size="2em"
            name="close"
            @click="close"
        /></a>
      </div>
      <t-dialog
        header="请确认"
        body="是否关闭应用？"
        :visible.sync="visible"
        :onConfirm="onConfirmAnother"
        :onCancel="onCancel"
        :onEscKeydown="onCancel"
        :onCloseBtnClick="onCancel"
        :onOverlayClick="onCancel"
        :onClose="onCancel"
      ></t-dialog>
    </div>
  </bgLayout>
</template>

<script>
import Layout from "@/layout/index";
import bgLayout from "@/layout/bgLayout/index";
import LoginCard from "@/components/loginCard";
import SignupCard from "@/components/signupCard";
import { Icon } from "tdesign-icons-vue";
import form from "@/utils/form";
export default {
  name: "login",
  data() {
    return {
      visible: false,
      isForm: form.IsForm(),
      needSignup: false,
    };
  },
  components: {
    Layout,
    bgLayout,
    LoginCard,
    SignupCard,
    Icon,
  },
  mounted() {},
  methods: {
    close() {
      this.visible = true;
    },
    onConfirmAnother() {
      form.CloseForm();
    },
    onCancel(context) {},
    showSignup() {
      this.needSignup = true;
    },
    showLogin() {
      this.needSignup = false;
    },
  },
};
</script>

<style lang="less">
@import "index";
</style>



