

window.addEventListener(
  "message", handler
);

let statusChangeEvents = [];

/// <summary>
/// 关闭窗体
/// </summary>
/// <returns></returns>
function CloseForm() {
  if (typeof form === 'undefined') return;
  form.CloseForm();
}

/// <summary>
/// 最小化窗体
/// </summary>
/// <returns></returns>
function MinimizeForm() {
  if (!IsForm()) return;
  form.MinimizeForm();
  StatusChange();
}

/// <summary>
/// 最大化或恢复正常窗体
/// </summary>
/// <returns></returns>
function MaximizeOrNomalForm() {
  if (!IsForm()) return;
  form.MaximizeOrNomalForm();
  StatusChange();
}
/// <summary>
/// 获取窗体宽度
/// </summary>
/// <returns></returns>
function GetFormWidth() {
  if (!IsForm()) return document.documentElement.clientWidth;
  return form.GetFormWidth();
}
/// <summary>
/// 获取窗体高度
/// </summary>
/// <returns></returns>
function GetFormHeight() {
  if (!IsForm()) return document.documentElement.clientHeight;
  return form.GetFormHeight();
}

/// <summary>
/// 获取窗体状态
/// </summary>
/// <returns></returns>
function GetFormStatus() {
  if (!IsForm()) return;
  return form.GetFormStatus();
}

/// <summary>
/// 是否是form
/// </summary>
/// <returns></returns>
function IsForm() {
  if (typeof form === 'undefined')
    return false;
  else
    return true;
}

//添加监听
function AddStatusChangeEventListener(fuc) {
  return statusChangeEvents.push(fuc);
}

//移除监听
function RemoveStatusChangeEventListener(fuc) {
  return statusChangeEvents.remove(fuc);
}

function StatusChange() {
  for (var i = 0; i < statusChangeEvents.length; i++) {
    try {
      statusChangeEvents[i]();
    }
    catch (e) {
      console.error(e)
    }
  }
}

function handler(event) {
  if (typeof event.data !== "undefined" && typeof event.data.action !== "undefined" && event.data.action == "MaximizeOrNomalForm")
    StatusChange();
}

export default {
  MinimizeForm,
  MaximizeOrNomalForm,
  GetFormHeight,
  GetFormWidth,
  GetFormStatus,
  AddStatusChangeEventListener,
  RemoveStatusChangeEventListener,
  IsForm,
  CloseForm
}




