<template>
  <div class="sign-up-card">
    <a href="javascript:;"
      ><icon
        class="t-menu__operations-icon sign-up-card-header-back"
        name="arrow-left"
        @click="back"
    /></a>
    <t-row>
      <t-col :span="12" class="sign-up-card-header">
        <h1>注册账号</h1>
      </t-col>
    </t-row>
    <t-divider></t-divider>
    <t-form
      :data="info"
      ref="form"
      @submit="signup"
      :colon="true"
      :labelWidth="0"
    >
      <div class="sign-up-card-centent">
        <t-row class="sign-up-card-centent-item">
          <t-col :span="12">
            <t-form-item name="account">
              <t-input
                clearable
                v-model="info.account"
                placeholder="请输入账号"
                @clear="onClear"
              >
                <user-add-icon slot="prefix-icon"></user-add-icon
              ></t-input> </t-form-item
          ></t-col>
        </t-row>
        <t-row class="sign-up-card-centent-item">
          <t-col :span="12">
            <t-form-item name="password">
              <t-input
                type="password"
                clearable
                v-model="info.password"
                placeholder="请输入密码"
              >
                <lock-on-icon
                  slot="prefix-icon"
                ></lock-on-icon> </t-input></t-form-item
          ></t-col>
        </t-row>
        <t-row class="sign-up-card-centent-item">
          <t-col :span="12">
            <t-form-item name="rePassword">
              <t-input
                type="password"
                clearable
                v-model="info.rePassword"
                placeholder="请再次输入密码"
              >
                <lock-on-icon
                  slot="prefix-icon"
                ></lock-on-icon> </t-input></t-form-item
          ></t-col>
        </t-row>
        <t-row class="sign-up-card-centent-item">
          <t-col :span="12">
            <t-form-item name="name">
              <t-input
                clearable
                v-model="info.phone"
                placeholder="请输入用户名"
              >
                <user-icon
                  slot="prefix-icon"
                ></user-icon> </t-input></t-form-item
          ></t-col>
        </t-row>
        <t-row class="sign-up-card-centent-item">
          <t-col :span="12">
            <t-form-item name="sex">
              <t-select
                v-model="info.sex"
                :options="sexs"
                placeholder="请选择性别"
              >
                <icon name="usergroup" slot="prefixIcon" />
              </t-select>
            </t-form-item>
          </t-col>
        </t-row>

        <t-row class="sign-up-card-centent-item">
          <t-col :span="12">
            <t-form-item name="phone">
              <t-input
                clearable
                v-model="info.phone"
                placeholder="请输入手机号"
              >
                <mobile-icon
                  slot="prefix-icon"
                ></mobile-icon> </t-input></t-form-item
          ></t-col>
        </t-row>
      </div>
      <t-divider></t-divider>
      <div class="sign-up-card-footer">
        <t-row>
          <t-col :span="12">
            <t-button
              theme="default"
              shape="round"
              variant="outline"
              style="margin: 9px"
              :loading="loading"
              type="submit"
            >
              <icon slot="icon" name="login" />
              注 册</t-button
            >
          </t-col>
        </t-row>
      </div>
    </t-form>
  </div>
</template>

<script>
import {
  Icon,
  LockOnIcon,
  UserIcon,
  MobileIcon,
  UserAddIcon,
} from "tdesign-icons-vue";
import account from "@/utils/account";
export default {
  name: "signupCard",
  data() {
    return {
      loading: false,
      info: {
        account: "",
        password: "",
        rePassword: "",
        phone: "",
        sex: "",
      },
      sexs: [
        { label: "男", value: 1 },
        { label: "女", value: 0 },
      ],
    };
  },
  components: {
    Icon,
    LockOnIcon,
    MobileIcon,
    UserAddIcon,
    UserIcon,
  },
  methods: {
    signup({ validateResult, firstError }) {
      this.loading = true;
      if (validateResult === true) {
        this.loading = false;
        this.$message.success({
          content: "提交成功",
          placement: "bottom-right",
        });
        this.$emit("login", {});
      } else {
        this.loading = false;
        console.log("Errors: ", validateResult);
        this.$message.warning({
          content: firstError,
          placement: "bottom-right",
        });
      }
    },
    onClear() {},
    back() {
      this.$emit("login", {});
    },
  },
};
</script>


<style lang="less">
@import "index";
</style>



